            {{ global.lang_pack.max }}
          </div>
        </div>

        <div class="team_text_part">
          <div class="team_idea">
            {{ global.lang_pack.max_idea }}
          </div>
          <div
            class="team_text_line team_text_line_2"
          ></div>
          <div :class="'content_text_'+global.lang">{{ global.lang_pack.max_intro }}
          </div>
        </div>
      </div>

      <div  class="team_part">
        <div class="team_img_part">
          <div class="team_img_box">
            <img
              class="team_img"
              src="/static/team/evan.png"
            />
          </div>
          <div
            class="team_text_line"
          ></div>
          <div class="member_name">
            {{ global.lang_pack.evan }}
          </div>
        </div>
        <div class="team_text_part">
          <div class="team_idea">
            {{ global.lang_pack.evan_idea }}
          </div>
          <div
            class="team_text_line team_text_line_3"
          ></div>
          <div :class="'content_text_'+global.lang">{{ global.lang_pack.evan_intro }}
          </div>
        </div>
      </div>
    </div>

    <div v-if="global.mode === 1">
      <div class="title_part">
        <div :class="'title_'+global.lang">{{ global.lang_pack.team }}</div>
      </div>

      <div class="team_part">
        <div class="team_img_part">
          <div class="team_img_box">
            <img
              class="team_img"
              src="/static/team/christy.png"
            />
          </div>
          <div
            class="team_text_line"
          ></div>
          <div class="member_name">
            {{ global.lang_pack.christy }}
          </div>
        </div>
        <div>
        </div>
        <div class="team_text_part">
          <div class="team_idea">
            {{ global.lang_pack.christy_idea }}
          </div>
          <div class="team_text_line"
          ></div>
          <div :class="'content_text_'+global.lang">{{ global.lang_pack.christy_intro }}
          </div>
        </div>
      </div>

      <div class="team_part">

        <div class="team_img_part">
          <div class="team_img_box">
            <img
              class="team_img"
              src="/static/team/max.png"
            />
          </div>
          <div
            class="team_text_line"
          ></div>
          <div class="member_name">
            {{ global.lang_pack.max }}
          </div>
        </div>

        <div class="team_text_part">
          <div class="team_idea">
            {{ global.lang_pack.max_idea }}
          </div>
          <div
