          ></div>
          <div :class="'content_text_'+global.lang">{{ global.lang_pack.max_intro }}
          </div>
        </div>
      </div>

      <div class="team_part">
        <div class="team_img_part">
          <div class="team_img_box">
            <img
              class="team_img"
              src="/static/team/evan.png"
            />
          </div>
          <div
            class="team_text_line"
          ></div>
          <div class="member_name">
            {{ global.lang_pack.evan }}
          </div>
        </div>
        <div class="team_text_part">
          <div class="team_idea">
            {{ global.lang_pack.evan_idea }}
          </div>
          <div
            class="team_text_line team_text_line_3"
          ></div>
          <div :class="'content_text_'+global.lang">{{ global.lang_pack.evan_intro }}
          </div>
        </div>
      </div>
    </div>

    <Footer></Footer>
    <Menu></Menu>
    <LangTransfer></LangTransfer>
  </div>
</template>

<style lang="css" scoped>
div.team_page {
  background: #f7f7f7;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  overflow-x: hidden;
}

div.team_part {
  margin-top: 100px;
  /*display: flex;*/
  display: grid;
  grid-template-columns: 486.667rem 973.333rem;
  /*如果只用grid，啥也不用，就有手机版的效果！！！！*/
  padding-left: 11.98%;
  padding-right: 11.98%;
}

div.team_img_part {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-row: 1;
  grid-column: 1;
}

div.team_img_box {
  width: 220px;
  height: 220px;
  overflow: hidden;
  border-radius: 100%;
  border: solid 5px #efcd24;
}

img.team_img {
  object-fit: cover;
  width: 220px;
  height: 220px;
  transition: all 0.2s;
  filter: grayscale(100%);
}

img.team_img:hover {
  filter: grayscale(0%);
  transform: scale(1.1);
}

div.member_name {
  font-size: 35px;
  display: inline-block;
}

div.team_text_part {
  grid-row: 1;
  grid-column: 2;
  /*margin-left: 100px;*/
  align-self: flex-start;
}

div.team_idea {
  font-size: 40px;
}

div.team_text_line {
  margin-top: 20px;
  width: 100px;
  display: inline-block;
  background: #37383d;
  position: relative;
  height: 1.5px;
}

@media screen and (max-width: 540px) {

}


</style>

<script>
import global from "../global";
import anime from "../../../modules/anime";
import Menu from "../../components/menu/Menu";
import LangTransfer from "../../components/lang_transfer/LangTransfer"
import Footer from "../../components/footer/Footer"

//export default
export default {
  name: "Team",
  data() {
    return {
      global: global,
    };
  },
  components: {
    Menu,
    Footer,
    LangTransfer
  },
  beforeCreate() {
    //set documentElement root size and language package
    global.refresh_windows_size();
    global.set_lang_pack();
  },
  created() {
  },
  mounted() {
    // mount the window size listen
    window.onresize = () => {
      return (() => {
        global.refresh_windows_size();
      })();
    };
    this.refresh_root_size();
    this.start_why_3_check_anime();
    Menu.methods.change_logo_color("#251714", "#efcd24", 1000);
  },
  destroyed() {
  },
  watch: {
    // watch the size change to refresh
    "global.width": function (val, oldVal) {
      this.refresh_root_size();
    },
  },
  methods: {
    refresh_root_size() {
      let static_piexl = 700;
      if (global.width < static_piexl) {
        document.documentElement.style.fontSize = static_piexl / 1920 + "px";
        return;
      }
      document.documentElement.style.fontSize = global.width / 1920 + "px";
    },
    start_why_3_check_anime() {
      anime({
        // targets:"div."
      });
    },
  },
};
</script>



